.form-label {
  @apply mb-2 block select-none;
    font-size: 15px;
}

.form-input,
.form-textarea,
.form-select {
  @apply leading-normal block w-full border bg-white font-sans rounded text-left appearance-none relative focus:border-indigo-400 focus:ring;
    padding: 3px 10px;
    font-size: 15px;
    height: 2.3rem;

  &::placeholder {
    @apply text-gray-500 opacity-100;
  }
}

.form-input{
    height: 2.7rem;
    &::placeholder {
        font-size: 13px;
    }
}

.form-textarea{
    height: inherit;
}

.form-select {
  @apply pr-6;

  background-image: url('data:image/png;base64,');
  background-size: 0.7rem;
  background-repeat: no-repeat;
  background-position: right 0.7rem center;

  &::-ms-expand {
    @apply opacity-0;
  }
}

.ticket-filters{
    .search{
        max-width: 35rem;
    }
    select.form-select, input{
        height: 44px;
    }
}

.form-input.error,
.form-textarea.error,
.form-select.error {
  @apply border-red-500 focus:ring focus:ring-red-200;
}

.form-error {
  @apply text-red-700 mt-2 text-sm;
}


.form-select{
    &:focus{
        border-color: #9b4dca;
        outline: 0;
    }
}

.btn{
    &.generate{
        background-color: #7366ff;
        height: 45px;
        color: #fff;
        border-radius: 4px;
        display: flex;
        justify-content: center;
    }
    &.print_download{
        background-color: #7366ff;
        height: 32px;
        font-size: 12px;
        font-weight: 300;
        color: #fff;
        border-radius: 3px;
        display: flex;
        justify-content: center;
        cursor: pointer;
    }
}
.hr_or{
    font-size: 12px;
    font-weight: 100;
    color: #111111;
    hr {
        border-color: #cdcdcd;
        background-color: #cdcdcd;
        border-width: 0.5px;
        width: 100%;
    }
}

.dark{

}
.grid{
    .list-none{
        svg{
            fill: #94a3b8;
        }
    }
}
.tab-head{
    ul{
        li{
            div{
                cursor: pointer;
                &.active{
                    border-color: #1c64f2;
                    color: #1c64f2;
                }
            }
        }
    }
}
.tab-content{
    .t-content{
        display: none;
        &.active{
            display: block;
        }
    }
}
.p-nat{
    a{
        &:hover, &:focus, &.active{
            background-color: #fff;
        }
    }
}
.dark{
    .p-nat{
        a{
            &:hover, &:focus, &.active{
                background-color: #1d1e26;
            }
        }
    }
}
